.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem 0;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  min-width: 100%;
  height: 80px;
}
a {
  text-decoration: none;
  color: #ccd6f6;
  border: none;
}
a:hover {
  color: #64ffda;
  cursor: pointer;
}
button {
  text-decoration: none;
  color: #ccd6f6;
  border: none;
  font-size: 16px;
}
button:hover {
  color: #64ffda;
  cursor: pointer;
}

.navigation {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
li {
  padding: 0 1rem;
}

.homeIcon {
  width: 64px;
  height: 64px;
  padding-left: 1rem;
}
.homeIcon path {
  transition: all 250ms ease-in;
}
.homeIcon:hover path {
  cursor: pointer;
  animation-name: hoverHome;
  animation-duration: 0.25s;
  animation-timing-function: ease-in;
  stroke: #0a192f;
  fill: #64ffda;
}
@keyframes hoverHome {
  0% {
    stroke: #64ffda;
    fill: #0a192f;
  }
  100% {
    stroke: #0a192f;
    fill: #64ffda;
  }
}
@media screen and (max-width: 700px) {
  .header {
    min-height: 250px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .homeIcon {
    display: none;
    width: 64px;
    height: 64px;
  }
  .navigation {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .navigation ul {
    flex-direction: column;
  }
  li {
    width: 100%;
    font-size: 16px;
    padding: 10px 1rem;
  }
  li:hover {
    border: 1px solid #64ffda;
    cursor: pointer;
    border-radius: 5px;
  }
}
